var fs = require("fs");

var MarkdownIt = require("markdown-it");

var Prism = require("prismjs");

var HeaderLinks = require("markdown-it-anchor");

var aliases = {
  js: "jsx",
  render: "jsx",
  "render-babel": "jsx",
  html: "markup",
  sh: "bash"
};
var supportedTypes = {
  render: true,
  "render-babel": true
};

var highlight = function highlight(str, lang) {
  if (lang) {
    highlightLang = aliases[lang] || lang;

    require("prismjs/components/prism-".concat(highlightLang, ".js"));

    if (Prism.languages[highlightLang]) {
      var highlighted = Prism.highlight(str, Prism.languages[highlightLang]);
      return supportedTypes[lang] ? appendScript(lang, str, highlighted) : highlighted;
    } else {
      return str;
    }
  } else {
    return str;
  }
};

var appendScript = function appendScript(lang, js, html) {
  var type = lang === "render-babel" ? "babel" : "javascript";
  var id = "render-".concat(Math.random().toString(32).substring(2));
  var script = lang === "render-babel" ? "\n(function() { var DOM_NODE = document.getElementById(\"".concat(id, "\");\n").concat(js, "\n})()") : "\ndocument.addEventListener(\"DOMContentLoaded\", function(event) { var DOM_NODE = document.getElementById(\"".concat(id, "\");\n").concat(js, "\n})");
  return "<pre class=\"render-js-code\"><code class=\"jsx\">".concat(html, "</code></pre>\n<div id=\"").concat(id, "\" class=\"render-js\"></div>\n<script type=\"text/").concat(type, "\">").concat(script, "</script>\n");
};

var md = new MarkdownIt({
  html: true,
  highlight: highlight
}).use(HeaderLinks, {
  permalink: true,
  permalinkBefore: true,
  permalinkSymbol: "#"
});

module.exports = function (source) {
  return md.render(source);
};